import React from 'react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import { motion } from 'framer-motion';

interface Message {
  text: string;
  isUser: boolean;
}

interface ResponseAreaProps {
  messages: Message[];
}

const ResponseArea: React.FC<ResponseAreaProps> = ({ messages }) => {
  return (
    <div className="space-y-4 p-4">
      {messages.map((message, index) => (
        <motion.div
          key={index}
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.3, delay: index * 0.1 }}
          className={`flex ${
            message.isUser ? 'justify-end' : 'justify-start'
          }`}
        >
          <div
            className={`p-3 rounded-md shadow-md max-w-3/4 ${
              message.isUser ? 'bg-blue-600 text-white' : 'bg-gray-800 text-white'
            }`}
          >
            <ReactMarkdown
              children={message.text}
              remarkPlugins={[remarkGfm]}
              components={{
                h1: ({ node, ...props }) => <h1 className="text-2xl font-bold mt-2 mb-2" {...props} />,
                h2: ({ node, ...props }) => <h2 className="text-xl font-bold mt-2 mb-2" {...props} />,
                h3: ({ node, ...props }) => <h3 className="text-lg font-bold mt-2 mb-2" {...props} />,
                a: ({ node, ...props }) => <a className="text-blue-300 hover:underline" {...props} />,
                p: ({ node, ...props }) => <p className="mb-2" {...props} />,
                ul: ({ node, ...props }) => <ul className="list-disc list-inside mb-2" {...props} />,
                ol: ({ node, ...props }) => <ol className="list-decimal list-inside mb-2" {...props} />,
                li: ({ node, ...props }) => <li className="mb-1" {...props} />,
              }}
            />
          </div>
        </motion.div>
      ))}
    </div>
  );
};

export default ResponseArea;
