"use client";
import React, { useState, useRef, useEffect } from 'react';
import PromptInput from './PromptInput';
import ResponseArea from './ResponseArea';
import { motion } from 'framer-motion';

interface Message {
  text: string;
  isUser: boolean;
}

const ChatInterface: React.FC = () => {
  const [messages, setMessages] = useState<Message[]>([]);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSend = async (prompt: string) => {
    // Add user message
    const newUserMessage: Message = { text: prompt, isUser: true };
      setMessages((prevMessages) => [...prevMessages, newUserMessage]);

    try {
      const response = await fetch('/api/analyze', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ code_snippet: prompt, analysis_type: 'explain' }),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      const aiResponse = data.analysis_result;
      const newAiMessage: Message = { text: aiResponse, isUser: false };
      setMessages((prevMessages) => [...prevMessages, newAiMessage]);
    } catch (error: any) {
      console.error('Error fetching data:', error);
      const errorMessage: Message = { text: `Error: ${error.message}`, isUser: false };
      setMessages((prevMessages) => [...prevMessages, errorMessage]);
    }
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: -20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3 }}
      className="flex flex-col space-y-4 p-4 rounded-2xl shadow-md bg-gray-800 w-full"
    >
      <ResponseArea messages={messages} />
      <PromptInput onSend={handleSend} />
      <div ref={messagesEndRef} />
      <div className="text-sm text-gray-400 mt-2">
        <p className="font-semibold text-blue-400">Training Guide:</p>
        <ul className="list-disc list-inside text-gray-400">
          <li>Write clear and concise prompts.</li>
          <li>Include instructions, context, and objectives.</li>
          <li>Use Markdown to format your responses.</li>
          <li>Experiment with different prompt types for better results.</li>
        </ul>
      </div>
    </motion.div>
  );
};

export default ChatInterface;
