"use client";
import React, { useState, ChangeEvent, KeyboardEvent } from 'react';

interface PromptInputProps {
  onSend: (prompt: string) => void;
}

const PromptInput: React.FC<PromptInputProps> = ({ onSend }) => {
  const [prompt, setPrompt] = useState('');

  const handleChange = (event: ChangeEvent<HTMLTextAreaElement>) => {
    setPrompt(event.target.value);
  };

  const handleKeyDown = (event: KeyboardEvent<HTMLTextAreaElement>) => {
    if (event.key === 'Enter' && !event.shiftKey) {
      event.preventDefault();
      handleSubmit();
    }
  };

  const handleSubmit = () => {
    if (prompt.trim()) {
      onSend(prompt.trim());
      setPrompt('');
    }
  };

  return (
    <div className="rounded-2xl shadow-md bg-gray-50 p-4">
      <textarea
        className="w-full rounded-xl p-3 border-2 border-gray-200 focus:border-blue-500 resize-none outline-none text-gray-800 placeholder-gray-400"
        placeholder="Escribe tu prompt aquÃ­..."
        value={prompt}
        onChange={handleChange}
        onKeyDown={handleKeyDown}
        rows={1}
      />
      <button
        className="mt-2 px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50 transition-colors duration-200"
        onClick={handleSubmit}
      >
        Send
      </button>
    </div>
  );
};

export default PromptInput;
